<template>
	<view class="">
		<load ref="inputLoad"></load>
		<popUp ref="inputpopUp"></popUp>
		<image class="bgc-picture1" src="../../static/fristPage/HomePage.png" mode="widthFix"></image>
		<view class="bgc-o-y" style="position: fixed; bottom: 0; z-index: -2; height: 100vh; width: 750rpx;"></view>		
		<!-- <view class="" style="height: 63.5vh;"></view>   -->
		<!-- 输入邮箱开始 -->
		<view v-show="enterEmailFlag" class="e-view1" style="padding-top: 63.5vh;"> 
			<view class="" style="height: 60rpx;"></view>
			<view class="ta-c f49rpx fw800 color-H0f649c lsp-5" style="">Enter your Email</view>
			<view class="b-r-15 df box-sha  bgc_FFF" 
				style="margin: 20rpx 46.5rpx 21rpx; height: 90rpx;"> 
				<view class="">
					<image src="../../static/fristPage/Iconmetro-mail.png" mode=""
					style="width: 32rpx; height: 26rpx; margin: 32rpx 27rpx 32rpx 42rpx;"></image> 	
				</view>
				<view class="df" style="align-items: center;">
					<input type="email"  placeholder="Email" cursor-spacing=10
					placeholder-class="input1" v-model.trim="inputEmails" class="fw800 f24rpx" 
					style=" width: 380rpx; caret-color: #ee5b30; color: #007AFF;" />  
				</view> 
			</view>  
			<view @click="Continue" hover-class="hover_a24" hover-start-time=0 hover-stay-time=150
			class="bgc-o-r ta-c f33rpx FFF fw800 v_Continue">Continue</view> 
			<view class="df-jcc-aic" style="margin: 32rpx 0 26rpx;">
				<view @click="goSignup" class="f33rpx fw800" 
				hover-class="hover" hover-start-time=0 hover-stay-time=150
				style="line-height: 28rpx; color: #0f649c; border-bottom: 3rpx solid #0f649c;">
					SIGN UP
				</view>
			</view>
			<view class="ta-c f43rpx fw800 color-H0f649c lsp-5" style="margin: 40rpx -70rpx 10rpx;">
				Welcome to TekyGo!’s World 
			</view>
		</view>  
		<!-- 输入邮箱结束 -->
	</view> 
</template> 

<script>
	export default{
		data(){
			return{
				enterEmailFlag:true,
				enterPassword:false,
				NoEmail:false,
				forgotPassword:false,
				inputEmail:'',
				inputEmails:'',
				getEmail:'',
				
			}
		},
		watch:{
			inputEmails(newval){
				this.inputEmail = newval
			}
		},
		mounted() {
			// console.log('是的撒的');
			let email = uni.getStorageSync('Email')
			// console.log(email);
			if(email != ''){
				this.inputEmails = email
			}
		}, 
		methods:{ 
			// clickToken(){
			// 	let token = uni.getStorageSync('token')
			// 	if(token != ''){
			// 		uni.removeStorageSync('token')
			// 	}
			// },
			goSignup(){
				uni.setStorageSync('ifmy',false)
				uni.redirectTo({
					url:'./noEmailFound'
				})
			},
			async Continue(){
				let that = this
				if(this.inputEmail != ''){
					// console.log(this.inputEmail);
					this.$refs.inputLoad.openLoad()
					const {data:res} = await this.$myRequest({
						url:'/api/auth/get'+'?email='+this.inputEmail,
					})
					console.log(res);
					
					if(res!= '') {
						this.$refs.inputLoad.closeLoad()
					}
					
					if(res.errno == 303){
						uni.setStorageSync('ifmy',true)
						uni.redirectTo({
							url:'./noEmailFound' 
						})
						// this.$refs.inputpopUp.openPopup(res.errmsg)
						return false
					}else if(res.succeeded){
						console.log('邮箱存在');
						console.log(that.inputEmail);
						uni.setStorageSync('Email',that.inputEmails)
						uni.redirectTo({ 
							url:'./inputPassword' 
						})
					}else if(res.errno != 303 && res.errno != 0){
						this.$refs.inputpopUp.openPopup(res.errmsg) 
						return false
					} 
				}
				else{
					this.$refs.inputpopUp.openPopup('Enter your email') 
					return false
				}
			},

			// 找回密码
			goForgotPassword(){
				this.enterPassword = false
				this.forgotPassword = true
			}
		}
	}
</script> 

<style scoped>
	page{
		background-color: #ffcd43;
	}
	.input1{
		font-size: 24rpx;
		font-family: myfonts;
		font-weight: 800;
		color: #91a1aa;
	}
	.e-view1{
		margin: 0 78.5rpx;
	}
	/* 图片背景 */
	.bgc-picture1{
		 z-index:-1; 
		 width:750rpx; 
		 height:100%; 
		 position: fixed;  
		 top:0;  
	}
	.v_Continue{
		margin: 0 41.5rpx; 
		height: 84rpx; 
		border-radius: 42rpx; 
		line-height: 84rpx; 
		letter-spacing: 2rpx; 
		box-shadow: 0rpx 0rpx 3rpx #000;
	}
	
</style>
